<template>
    <p-card>
        <template #content>
            <div class="p-fluid">
                <div class="p-field p-grid">
                    <label
                        for="firstname"
                        class="p-col-fixed p-text-bold p-mb-2 p-md-2 p-mb-md-0"
                        style="width: 100px; font-size: 16px"
                    >
                        Female
                    </label>
                    <div class="p-col">
                        <p-auto-complete
                            v-model="demographic"
                            :suggestions="fillteredDemographic"
                            @complete="searchDemographic($event)"
                            @item-select="onDemographicSelect($event)"
                            field="label"
                            placeholder="Type to search"
                        />
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, onMounted, reactive, ref } from "vue";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import { PatientType } from "@/config.js";

import Service from "@/service/art-cycle.js";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();

        const title = ref("Female");
        const demographic = ref();
        const fillteredDemographic = ref();
        const loading = inject("loading");

        const searchDemographic = (event) => {
            Service.searchDemographic({
                term: event.query.trim(),
                sex: "F".toLowerCase()
            })
                .then((resp) => {
                    fillteredDemographic.value = resp.data;
                })
                .catch((err) => {
                    emitter.emit(EVENT_STRINGS.LOGOUT);
                });
        };

        const onDemographicSelect = ({ value: { demographicNo } }) => {
            loading.value = true;
            store
                .dispatch("artCycle/getCyclesDetails", demographicNo)
                .then(() => (loading.value = false));
        };

        onMounted(() => {
            let patient = store.state.artCycle.patient;
            if (patient) {
                demographic.value = {
                    ...patient,
                    label: `${patient.id} - ${patient.firstName} ${patient.lastName}`
                };
            }
        });

        return {
            title,
            demographic,
            fillteredDemographic,
            searchDemographic,
            onDemographicSelect
        };
    }
};
</script>
