<template>
    <p-data-table
        :value="cyclesSummary"
        dataKey="cycleId"
        v-model:selection="selectedRow"
        selectionMode="single"
        @row-select="onRowSelect"
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :scrollable="true"
        scrollHeight="flex"
        sortField="cycleNo"
        :sortOrder="-1"
        :loading="loading"
    >
        <p-column selectionMode="single" style="max-width: 3rem" />
        <p-column field="cycleId" header="#" style="max-width: 120px" />
        <p-column field="cycleNo" header="Cycle No" style="max-width: 120px" />
        <p-column
            field="cycleType"
            header="Cycle Type"
            style="max-width: 120px"
        />
        <p-column
            field="cycleNoType"
            header="Cycle No Type"
            style="max-width: 120px"
        />
        <p-column field="funded" header="Funded" style="max-width: 7rem">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.funded"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column field="cancel" header="Cancel" style="max-width: 7rem">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cancel"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cycleDate"
            header="Cycle Date"
            style="max-width: 120px"
        >
            <template #body="{ data }">
                {{ data.cycleDate.split(" ")[0] }}
            </template>
        </p-column>
        <p-column
            field="incomplete"
            header="Incomplete"
            style="max-width: 7rem"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.incomplete"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="cycleCompleted"
            header="Complete"
            style="max-width: 7rem"
        >
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.cycleCompleted"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column
            field="spermSource"
            header="Sperm Source"
            style="max-width: 120px"
        />
        <p-column
            field="eggSource"
            header="Egg Source"
            style="max-width: 120px"
        />
        <p-column field="eggDonor" header="Egg Donor" style="max-width: 120px">
            <template #body="{ data }">
                <p-checkbox
                    v-model="data.eggDonor"
                    :disabled="true"
                    :binary="true"
                />
            </template>
        </p-column>
        <p-column header="" />

        <template #footer>
            <p-button label="Save Cycle" class="p-mr-1" @click="save" />
            <p-button label="New Cycle" class="p-mr-1" @click="newCycle" />
            <p-button label="Merge Cycle" class="p-mr-1" />
            <p-button
                label="Delete Cycle"
                class="p-mr-1"
                @click="deleteCycle"
            />
            <p-button label="PDF Report" class="p-mr-1" />
            <p-button label="Other Blood Test" class="p-mr-1" />
            <p-button label="Import HL7 Message" class="p-mr-1" />
            <p-button label="Cryostorage Report" class="p-mr-1" />
            <p-button label="Copules Report" class="p-mr-1" />
            <p-button
                label="Create Born"
                class="p-mr-1 p-button-help"
                v-if="isCreateBornCycle"
                @click="createBornCycle"
            />
        </template>
    </p-data-table>
</template>

<script>
import { computed, inject, onMounted, onUnmounted, ref, watch } from "vue";
import { createNamespacedHelpers, useStore } from "vuex";
import { emitter } from "@/event-bus";

const { mapGetters } = createNamespacedHelpers("artCycle");

export default {
    setup() {
        const store = useStore();
        const loading = inject("loading", false);

        const selectedRow = ref();
        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );
        const isCreateBornCycle = ref(false);

        const onRowSelect = ({ data }) => {
            store.commit("artCycle/SET_SELECTED_CYCLE", data);
        };

        const setFirstOneSelected = (_cycles) => {
            let topCycleIndex = _cycles.findIndex(
                (item) => item.cycleNo == _cycles.length
            );
            selectedRow.value = cyclesSummary.value[topCycleIndex];
            store.commit("artCycle/SET_SELECTED_CYCLE", selectedRow.value);
        };

        const save = () => {
            emitter.emit("cycle-table:save-cycle");
        };

        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );
        watch(selectedCycle, (newValue) => {
            selectedRow.value = newValue;
            if (newValue.isBornCycle && newValue.bornCycleId == null) {
                isCreateBornCycle.value = true;
            } else {
                isCreateBornCycle.value = false;
            }
        });

        const newCycle = () => {
            loading.value = true;
            store
                .dispatch("artCycle/createNewCycle")
                .then(() => (loading.value = false));
        };

        const deleteCycle = () => {
            loading.value = true;
            store
                .dispatch("artCycle/deleteCycle", selectedCycle.value.cycleId)
                .then(() => {
                    loading.value = false;
                });
        };

        const createBornCycle = () => {
            isCreateBornCycle.value = false;
            const cycle = store.state.artCycle.selectedCycle;
            const born = {
                cycleId: cycle.cycleId,
                cycleName: cycle.cycleType,
                pNumber: cycle.patientId,
                createdBy: "lana",
                updatedBy: "lana",
                parentCycleType: "a",
                cycleNumber: cycle.cycleNo,
                firstName: store.state.artCycle.patient.firstName,
                lastName: store.state.artCycle.patient.lastName,
                sex: "f",
                yearOfBirth: store.state.artCycle.patient.year,
                monthOfBirth: store.state.artCycle.patient.month,
                dateOfBirth: store.state.artCycle.patient.day
            };

            store
                .dispatch("artCycle/createNewBornCycle", born)
                .then((data) => {
                    store.commit("artCycle/SET_BORN_CYCLE", {
                        cycleId: cycle.cycleId,
                        bornCycleId: data.bornCycleId
                    });
                })
                .catch(() => {
                    isCreateBornCycle.value = true;
                });
        };

        onMounted(() => {
            emitter.on("artCycleStore:detail-loaded", setFirstOneSelected);
            selectedRow.value = store.state.artCycle.selectedCycle;
        });

        onUnmounted(() => {
            emitter.off("artCycleStore:detail-loaded", setFirstOneSelected);
        });

        return {
            cyclesSummary,
            selectedRow,
            onRowSelect,
            save,
            newCycle,
            deleteCycle,
            loading,
            isCreateBornCycle,
            createBornCycle
        };
    }
};
</script>

<style scoped>
.p-datatable {
    background-color: white;
}

.p-datatable :deep() .p-datatable-tbody > tr.p-highlight {
    background-color: #052c65;
    color: white;
}
</style>
