<script>
import { computed, onMounted, reactive, ref, watch, inject } from "vue";
import { emitter } from "@/event-bus";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();
        const selectedCycle = computed(
            () => store.state.artCycle.selectedCycle
        );

        const dropdownOpts = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" }
        ]);

        const cycleTypeOptions = computed(
            () => store.state.artCycle.cycleTypeOptions
        );
        const spermSourceOptions = computed(
            () => store.state.artCycle.spermSourceOptions
        );
        const eggSourceOptions = computed(
            () => store.state.artCycle.eggSourceOptions
        );

        const cycle = reactive({
            cycleId: null,
            cycleType: null,
            funded: null,
            cancelled: null,
            spermSource: null,
            eggSource: null,
            eggDonor: null,
            complete: null
        });

        const fillupForm = (data) => {
            cycle.cycleId = data.cycleId;
            cycle.cycleType = data.cycleType;
            cycle.cancelled = data.cancel;
            cycle.spermSource = data.spermSource;
            cycle.eggSource = data.eggSource;
            cycle.complete = data.cycleCompleted;
        };

        watch(selectedCycle, fillupForm);

        onMounted(() => {
            if (selectedCycle.value) {
                fillupForm(selectedCycle.value);
            }
            store.dispatch("artCycle/getCycleFormFieldOptions");
        });
        const loading = inject("loading");
        const saveCycle = () => {
            loading.value = true;
            store
                .dispatch("artCycle/updateCycle", {
                    cycleNo: selectedCycle.value.cycleNo,
                    cycleId: selectedCycle.value.cycleId,
                    cycleType: cycle.cycleType,
                    cycleNoType: cycle.cycleNoType,
                    spermSource: cycle.spermSource,
                    eggSource: cycle.eggSource,
                    eggDonor: !!cycle.eggDonor,
                    cycleIncomplete: !cycle.complete,
                    cycleComplete: !!cycle.complete,
                    ohip: !!cycle.funded,
                    cycleCancelled: !!cycle.cancelled,
                    others: cycle.others,
                    cycleDate: selectedCycle.value.cycleDate
                })
                .then(() => {
                    return store.dispatch(
                        "artCycle/getCyclesDetails",
                        store.state.artCycle.patient.id
                    );
                })
                .catch(() => {})
                .finally(() => {
                    loading.value = false;
                });
        };

        emitter.on("cycle-table:save-cycle", saveCycle);

        return {
            dropdownOpts,
            cycleTypeOptions,
            spermSourceOptions,
            eggSourceOptions,
            cycle
        };
    }
};
</script>

